@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    padding: calc(v.$separacion / 2) 0;
    margin-bottom: 0;
    background-color: v.$grisOscuro;

    .contenido-footer{
        padding-top: calc(v.$separacion / 2);
    }

    .navegacion {
        visibility: visible;
        opacity: 1;
        height: auto;
    }

    .copyright{
        color: v.$blanco;
        text-align: center;
    }
}